<script>
    export default {
        name: '',
        data() {
            return {
                active_menu_cars_park: true,
                active_menu_cars: false,
                active_menu_drivers: false,
                active_menu_travels: false,
                active_menu_groups: false,

                active_menu_pay: true,
                active_menu_auto_pay: false,
                active_menu_req_pay: false,

                active_menu_setting: true,
                active_menu_setting_global: false,
                active_menu_users: false,
                active_menu_agregators: false,

                active_menu_profile: false,


                hide_elem: false,
            }
        },
        props: {
        },
        methods: {
            show_hide_sidebar() {
                this.hide_elem = !this.hide_elem;
            },
            show_obj_menu_cars_park() {
                this.active_menu_cars_park = !this.active_menu_cars_park;
            },

            show_obj_menu_pay() {
                this.active_menu_pay = !this.active_menu_pay;
            },

            show_obj_menu_setting() {
                this.active_menu_setting = !this.active_menu_setting;
            },

            show_obj_menu_profile() {
                this.active_menu_profile = !this.active_menu_profile;
            },

            check_page() {
                let pages_cars_park = ['drivers-page', 'cars-page', 'travels-page', 'groups-page'].indexOf(this.$route.name)
                let pages_pay = ['pay-auto-page', 'pay-req-page'].indexOf(this.$route.name)
                let pages_settings = ['settings-page', 'users-page', 'agregators-page'].indexOf(this.$route.name)

                if (pages_cars_park == 0) {this.active_menu_drivers = true;} 
                if (pages_cars_park == 1) {this.active_menu_cars = true;}
                if (pages_cars_park == 2) {this.active_menu_travels = true;}
                if (pages_cars_park == 3) {this.active_menu_groups = true;}
                if (pages_cars_park == -1) {this.active_menu_cars_park = false;}

                if (pages_pay == 0) {this.active_menu_auto_pay = true;} 
                if (pages_pay == 1) {this.active_menu_req_pay = true;} 
                if (pages_pay == -1) {this.active_menu_pay = false;}

                if (pages_settings == 0) {this.active_menu_setting_global = true;} 
                if (pages_settings == 1) {this.active_menu_users = true;} 
                if (pages_settings == 2) {this.active_menu_agregators = true;} 
                if (pages_settings == -1) {this.active_menu_setting = false;}  
            },
        },
        mounted() {
            this.check_page();
        }
    }   
</script>

